import React, { useState } from "react";
import Paper from "@mui/material/Paper";
import TextField from "@mui/material/TextField";
import "./RegisterOrLogin.css";
import Button from "@mui/material/Button";
import Axios from "axios";
import Alert from '@mui/material/Alert';

export default function Login(props) {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [showAlert,setShowAlert]=useState(false);
  const [registerSuccess,setRegisterStatus]=useState(false);

  function handleUsernameChange(event) {
    setUsername(event.target.value);
  }

  function handlePasswordChange(event) {
    setPassword(event.target.value);
  }

  function handleLogin(event){
    Axios.post("/login",{username:username,password:password}).then((res)=>{
      console.log(res.data);
      if(res.data.login==="success"){
        props.checkStatus();
      }
      // else{
      //   setShowAlert(true);
      // }
      // // props.checkStatus();
    })
  }
  function handleRegister(event){
    Axios.post("/register",{username:username,password:password}).then((res)=>{
      if(res.data.register==="success"){
        setRegisterStatus(true);
        setShowAlert(true);
      }else{
        setShowAlert(true);
      }
      // props.checkStatus();
    })
  }

  return (
    <div
      style={{
        display: "flex",
        justifyContent: "center",
        flexWrap: "wrap",
        width: "400px",
      }}
    >
      <Paper
        elevation={3}
        className="title"
        style={{ backgroundColor: "#9A86A4", color: "white" }}
      >
        Login
      </Paper>
      <Paper
        elevation={3}
        className="roltopContainer"
        style={{
          padding: "25px",
          margin: "25px 0 0",
          display: "flex",
          justifyContent: "space-evenly",
          alignItems: "center",
          flexWrap: "wrap",
          marginBottom:"25px"
        }}
      >
        <TextField
          style={{ marginBottom: "20px" }}
          className="username"
          label="Username"
          value={username}
          onChange={handleUsernameChange}
          variant="outlined"
        />
        <TextField
          style={{ marginBottom: "20px" }}
          className="password"
          label="Password"
          value={password}
          onChange={handlePasswordChange}
          variant="outlined"
        />
        <div className="lorButtons">
        <Button className="lorButton" variant="contained" onClick={handleLogin}>
          Login
        </Button>
        <Button className="lorButton" variant="contained" onClick={handleRegister}>
          Register
        </Button>
      </div>
      </Paper>
      {showAlert?(registerSuccess?<Alert severity="success">You are registered successfully, please login</Alert>:<Alert severity="error">You are not registered, try again" </Alert>):null}
    </div>
  );
}
